.fieldset {
  padding: 0;
  margin: 0;
  width: 100%;
  border: none;
}

.legend {
  width: 100%;
  padding: 10px 0 5px;
  border-bottom: 1px solid var(--colors-interface-yorkshire-cloud);
}

.content {
  display: flex;
  gap: 12px;
  padding: 12px 0;
}

.content[data-view="COLUMN"] {
  flex-direction: column;
}

.content[data-view="ROW"] {
  flex-direction: row;
}

.content[data-view="TWO_COLUMNS"] {
  display: grid;
  grid-auto-flow: row;
  grid-template-columns: repeat(2, minmax(0, 192px));
}
